import React, { Component } from 'react'
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, Button, CardMenu, IconButton, CardText } from 'react-mdl';

class Projects extends Component {
  constructor(props){
    super(props);
    this.state = { activeTab: 0 };
  }
  toggleCategories(){
    if(this.state.activeTab === 0){
      return(
        <div className="projects-grid">
            {/* Project 1 */}
        <Card shadow={5} style={{minwidth: '450', margin: 'auto' }}>
          <CardTitle style={{color: 'black', height: '176px',
           background: 'url(https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fudemy-images.udemy.com%2Fcourse%2F750x422%2F1075570_475b_2.jpg&f=1) center / cover'}}>
           React Project # 1</CardTitle>
           <CardText>
             loremsakjdkjasjdlksajlkdjlksajdklsajlk
           </CardText>
           <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>CodePen</Button>
              <Button colored>Live Demo</Button>
           </CardActions>
           <CardMenu style={{color: '#fff'}}>
             <IconButton name='share' />
           </CardMenu>
        </Card>

        {/* Project 2 */}
        <Card shadow={5} style={{minwidth: '450', margin: 'auto' }}>
          <CardTitle style={{color: 'black', height: '176px',
           background: 'url(https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fudemy-images.udemy.com%2Fcourse%2F750x422%2F1075570_475b_2.jpg&f=1) center / cover'}}>
           React Project # 2</CardTitle>
           <CardText>
             loremsakjdkjasjdlksajlkdjlksajdklsajlk
           </CardText>
           <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>CodePen</Button>
              <Button colored>Live Demo</Button>
           </CardActions>
           <CardMenu style={{color: '#fff'}}>
             <IconButton name='share' />
           </CardMenu>
        </Card>

        {/* Project 3 */}
        <Card shadow={5} style={{minwidth: '450', margin: 'auto' }}>
          <CardTitle style={{color: 'black', height: '176px',
           background: 'url(https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fudemy-images.udemy.com%2Fcourse%2F750x422%2F1075570_475b_2.jpg&f=1) center / cover'}}>
           React Project # 3</CardTitle>
           <CardText>
             loremsakjdkjasjdlksajlkdjlksajdklsajlk
           </CardText>
           <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>CodePen</Button>
              <Button colored>Live Demo</Button>
           </CardActions>
           <CardMenu style={{color: '#fff'}}>
             <IconButton name='share' />
           </CardMenu>
        </Card>
        </div>

      )
    }else if(this.state.activeTab === 1){
      return(
        <div>
          <h1>This is Node</h1>
        </div>
      )
    }else if(this.state.activeTab === 2){
      return(
        <div>
          <h1>This is Express</h1>
        </div>
      )
  }else if(this.state.activeTab === 3){
    return(
      <div>
        <h1>This is MongoDB</h1>
      </div>
    )
  }else if(this.state.activeTab === 4){
    return(
      <div>
        <h1>This is EJS</h1>
      </div>
    )
  }
}
  render() {
    return (
      <div className="category-tabs">
        <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
        <Tab>React</Tab>
        <Tab>Node</Tab>
         <Tab>Express</Tab>
         <Tab>MongoDB</Tab>
         <Tab>EJS</Tab>
        </Tabs>

          <Grid >
            <Cell col={12}>
              <div className="content">{this.toggleCategories()}</div>
            </Cell>
          </Grid>

      </div>
    )
  }
}
export default Projects;