import React, { Component } from 'react'
import { Grid, Cell} from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skills from './skills';

class Resume extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={4}>
            <div style={{ textAlign: 'center' }}>
              <img 
                src="https://www.shareicon.net/download/2015/09/18/103157_man_512x512.png"
                alt="avatar"
                style={{height: '200px'}}
              />
            </div>

            <h2 style={{paddingTop: '2em'}}>Abdul Ahad</h2>
            <h4 style={{color: 'grey'}}>Web Developer</h4>
            <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
            <p>dsakgfkjahjdlsanoidjsalijlj</p>
            <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
            <h5>Adress</h5>
            <p>menlo park, 9482</p>
            <h5>Phone</h5>
            <p>(1234) 456-789</p>
            <h5>Email</h5>
            <p>Someone@example.com</p>
            <h5>Web</h5>
            <p>mywebsite.com</p>
            <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
          </Cell>
          <Cell className="resume-right-col" col={8}>
            <h2>Education</h2>

            <Education 
            startYear = {2012}
            endYear = {2006}
            schoolName = "My University"
            schoolDescription = "aksjdasld"
            />

            <Education 
            startYear = {2012}
            endYear = {2006}
            schoolName = "My University"
            schoolDescription = "aksjdasld"
            />
            <hr style={{borderTop: "3px solid #e2294"}} />

            <h2>Experience</h2>
            <Experience 
            startYear = {2009}
            endYear = {2012}
            jobName = "First Job"
            jobDescription = "lkdsjfldsjflk"
            />

            <Experience 
            startYear = {2009}
            endYear = {2012}
            jobName = "First Job"
            jobDescription = "lkdsjfldsjflk"
            />
            <hr style={{borderTop: "3px solid #e2294"}} />
            
            <h2>Skills</h2>
            <Skills 
              skill="JavaScript"
              progress={100}
              /> 

              <Skills
              skill="React"
              progress={60} 
              />

              <Skills
              skill="Node"
              progress={80}
              />

              <Skills
              skill="MEAN"
              progress={90}  
              />

          </Cell>
        </Grid>
      </div>
    )
  }
}
export default Resume;